<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                典型案例
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <md-scroll-view :dragable="false" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.coverPicture" alt="">
                        <div class="article">
                            <p v-text="i.title"></p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">{{ i.updateTime }}</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/information'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, Icon, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 13,
      // tab项
      items: [{ name: 13, label: '活动' }, { name: 14, label: '技术对接' }, { name: 15, label: '成果转化' }, { name: 16, label: '设备共享' }],
      list: [],
      isFinished: false,
      Height: '900px',
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        typeId: 13
      }
    }
  },
  mounted () {
    this.Height = window.innerHeight - 84 + 'px'
    this.getList()
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.typeId = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/case/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/home')
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position absolute
        top 88px
        width 100%
        .item-cover
            width 100%
            background-color rgba(166, 166, 166, 0.1)
            border-radius 12px
            margin 30px auto
            .title
                height 85px
                line-height 85px
                font-size 32px
                color #333333FF
                border-bottom 1px solid rgba(166,166,166,1)
            .content
                padding-bottom 15px
                section
                    margin 30px
                    text-align left
                    text-indent 40px
                    /*color #A6A6A6FF*/
                    color rgba(102,102,102,0.8)
                    span
                        font-weight bold
                        color #333333FF
                .img
                    height 344px
                    background url("../../assets/device/img@2x.png") no-repeat
                    background-size 100% 100%
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 440px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.top-tab-bar .md-tab-bar .md-tab-bar-item
        width 25%!important
    /deep/.top-tab-bar .md-tab-bar .md-tab-bar-item:nth-child(2).is-active
        background url("../../assets/news/common_tab_s_03@2x.png") no-repeat!important
        background-size 100% 100%!important
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 50px 20px
    .scroll-view-item
        margin-bottom 30px
        text-align center
        font-size 28px
        font-family DINAlternate-Bold
        height 174px
</style>
